.popup{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:#2a2a2a;
  background:rgba(10,10,10,0.7);
  display:none;
  z-index:9999;
}

.popup .popup-close{
  position:absolute;
  top:10px;
  right:10px;
  font-size:24px;
  color:#ccc;
  z-index:20;
  cursor:pointer;
}

.popup .popup-close:hover{
  color:#fff;
}

.popup .popup-display{
  position:absolute;
  top:50%;
  left:50%;
  z-index:10;
  overflow:auto;
}

.popup .popup-display.no-scroll,
.popup .popup-display iframe.no-scroll{
  overflow:hidden;
}
