 .sa-collapse .sa-click {
    cursor:pointer;
    margin-bottom:0 !important;
}
.sa-collapse .sa-click :last-child {
    margin-bottom:0 !important;
}
.sa-collapse .sa-content {
    padding-top:0 !important;
    padding-bottom:0 !important;
}
.sa-collapse .sa-content > :last-child {
    margin-bottom:0 !important;
}
.sa-collapse:not(.active)>.sa-click>.sa-on {
    display:none;
}
.sa-collapse:not(.active)>.sa-click>.sa-off {
    display:block;
}
.sa-collapse.inactive > .sa-click .sa-on, .sa-collapse.inactive > .sa-click .sa-off {
    display:none;
}
.sa-collapse.inactive > .sa-click {
    cursor:auto;
}
.sa-collapse.inactive.hide-click > .sa-click {
    display:none;
}
